import React from 'react'
import PropTypes from 'prop-types'

export const AnalysisContainer = (props) => {
  const { amNum, pmNum, nightNum } = props
  return <div className="analysis-container bg-white">
    <div className="analysis-panel">
      <div className="ana-head">
        <h3 className="ana-title">000 ~ 1200</h3>
        <span className="desc">(){amNum}</span>
      </div>
    </div>
    <div className="analysis-panel">
      <div className="ana-head">
        <h3 className="ana-title">1200 ~ 1800</h3>
        <span className="desc">(){pmNum}</span>
      </div>
    </div>
    <div className="analysis-panel">
      <div className="ana-head">
        <h3 className="ana-title">1800 ~ 000</h3>
        <span className="desc">(){nightNum}</span>
      </div>
    </div>
  </div>
}

AnalysisContainer.propTypes = {
  amNum: PropTypes.number,
  pmNum: PropTypes.number,
  nightNum: PropTypes.number
}

export default AnalysisContainer



